async function getRepos() {
    const res = await fetch('https://api.github.com/users/RohanrajeBhosale/repos?sort=updated', { next: { revalidate: 3600 } });
    const data = await res.json();
    return Array.isArray(data) ? data.filter(repo => !repo.fork && repo.description).slice(0, 6) : [];
}

export default async function Projects() {
    const repos = await getRepos();

    return (
        <div className="container">
            <h2 style={{ fontSize: '3rem', fontWeight: 900, marginBottom: '40px' }}>Projects</h2>
            <div className="card-grid">
                {repos.map(repo => (
                    <a key={repo.id} href={repo.html_url} target="_blank" className="card">
                        <h3>{repo.name.replace(/-/g, ' ')}</h3>
                        <p>{repo.description}</p>
                        <div style={{ marginTop: '15px', color: 'var(--accent-color-1)', fontSize: '0.8rem' }}>
                            View Repository â†’
                        </div>
                    </a>
                ))}
            </div>
        </div>
    );
}